import React from "react";
import Task from "./Task.js";
import Task_1 from "./Task_1.js";
import Task_2 from "./Task_2.js";

//----Задача №1----

function App() {
  const array = [22, 0, -45, 1001, 8, -6, -0.1, 101, 2.5, -55555];

  const positive = array.filter(function (namber) {
    if (namber >= 0) {
      return true;
    }
    return false;
  });

  return (
    <div>
      <div>{positive}</div>
      <Task />
      <Task_1 />
    </div>
  );
}

export default App;
