import React from "react";

//----Задача №2----

function Task() {
  const messages = [
    { message: "hello", error: true },
    { message: "javascript", error: false },
    { message: "intocode", error: true },
    { message: "react", error: true },
    { message: "angular", error: false },
    { message: "es6", error: false },
  ];
  const newMessage = messages.filter(function (item) {
    if (item.error == false) {
      return true;
    }
    return false;
  });
  console.log(newMessage);

  return <div></div>;
}
export default Task;
