import React from "react";

//----Задача №3----

function Task_1() {
  const words = [
    "intocode",
    "js",
    "react",
    "css",
    "angular",
    "bootstrap",
    "html",
  ];
  const newWord = words.map(function (word) {
    if (word.length < 5) {
      return "*";
    }
    return word;
  });

  return <div>{newWord}</div>;
}
export default Task_1;
